/* 메뉴 */

@font-face {
    font-family: 'SBAggroB';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SBAggroB.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


a {
  text-decoration: none;
}
.navmenu {
      font-family: 'SBAggroB';

  background-color: rgba(0, 0, 0, 0.6);
  max-width: 100vw;
  display: flex;
  align-items: center;
  height: 100px;
  box-sizing: border-box;
  padding: 0;
  margin: 0px auto;
  
}
.temp {
  line-height: 100px;
  width: 10vw;
  text-align: center;

}
.navmenu ul {
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  width: 75vw;
  padding: 0;
  text-align: center;
}
.navmenu li {
  color: #FFF;
  width: 10%;
  height: 100px;
  line-height: 100px;
}
.navmenu li:hover {
    border-bottom: 5px solid red;
    transform: translateY(5px);


}
.nav-link {
  color: #FFF;
  border-radius: 10px;
  padding: 10px;
}


.nav-link:hover {    
  font-size: 200%;
  background-color: #99ACE9;
  transition: 0.2s ease-out;
}